ct in the community. We are the voice of innovation, ambassadors of humanity, agents of service and the movers and shakers of society."),_c('br'),_vm._v(" Over the past 54 years, we have been consistent in creating a legacy of responsible youth and have never hesitated to be ahead of our time. Our efforts in the field of education, women empowerment, environment conservation, mental health, water conservation and medical care are second to no other youth organization in the city. We have also encouraged cultural exchanges and professional conferences to broaden the minds of the members. Even during the pandemic, Rotaract District 3142 stepped up and was working alongside the government in all areas of service related to COVID-19, as the District Rotaract Representative for the year 2021-2022, I can confirm that we will continue to render our support to society and keep grooming future leaders to become confident custodians of this great nation. When the world around you is dark, it’s the best time to shine. It’s the year to become a ChangeMaker. ")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"body_wrapper"},[_c('div',{staticClass:"body_inner_left_holder"},[_c('article',{staticClass:"material-card Red"},[_c('h2',[_c('span',[_vm._v("Rtn. Rtr. Amit Lad")]),_c('strong',{staticStyle:{"font-size":"15px"}},[_vm._v(" Immediate Past District Rotaract Representative ")])]),_c('div',{staticClass:"mc-content"},[_c('div',{staticClass:"img-container"},[_c('img',{staticClass:"img-responsive",attrs:{"src":require("../../assets/ct/Amit.jpg")}})])])])]),_c('div',{staticClass:"body_inner_right"},[_vm._v(" Every year, a new leader steps in with a fresh vision and I did the same too. I was all set, built the trust amongst my people that I certainly will be a Game Changing DRR. I did everything I could and planned by my year. But, then, sometimes things are not in our control. But, even in these challenging times as Pandemic, we served the community by extending our support by conducting projects like HOPE, we were in position to help the people who were in need, either in medical or non medical emergencies. We successfully conducted professional development initiatives such as NEXT - The Conclave and for the first time we were able to conduct Panel discussions. We facilitated Digital sports sessions like fitness and self defence sessions and then we also facilitated basketball workshop. When we look at cultural exchanges, we also conducted Explore as our International Service Initiative, this year. We also made good amount of efforts to boost our social media engagements this year. Then, we also presented our Rotaractors with a literary platform this year for the very first time, Slice of Life, wherein they penned down emotional experiences (fictional and non-fictional) from their journeys. This is the best thing about Rotaract that it gives you exposure in maximum possible avenues and it does open up endless opportunities for everyone; we took up the leadership and as it is transpired, we all have been able to gain plethora of skills, which have always helped us become better humans in our Rotaract, personal and professional life and this is how it Rotaract leaves a significant mark in our lives. ")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"body_wrapper"},[_c('div',{staticClass:"body_inner_left_holder"},[_c('article',{staticClass:"material-card Red"},[_c('h2',[_c('span',[_vm._v("Rtn. Rtr. Adil Khan")]),_c('strong',[_vm._v(" DRR Elect | Deputy DRR & District Zonal Chair ")])]),_c('div',{staticClass:"mc-content"},[_c('div',{staticClass:"img-container"},[_c('img',{staticClass:"img-responsive",attrs:{"src":require("../../assets/ct/Adil.jpg")}})])])])]),_c('div',{staticClass:"body_inner_right"},[_vm._v(" With the motto of \"Self-development through fellowship and service,\" it should be our prime focus to promote an 